import React from 'react'
import { Link } from 'gatsby'
import '../components/Layout.css'
import p from "../resource/favico@0.25x.png"

const Layout = ({children}) => {
    return (
        <>
            <nav>
                <div class="topnav">
                    <img class="image5" src={p}></img>
                <Link to='/'>Home</Link>              
                </div>
            </nav>
            <main>
                <div class="content">
                <br/>
                {children}
                <br/>
                <br/>
                <br/>
                <br/>
                </div>
                <div class="footer">
                 <p>Demo Website for Baron co management dashboard. all content are subjected to change. Not for Public Use, all Rights Reserved.</p>
                
                </div>
            </main>
        </>
    )
}
export default Layout